import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect, useState } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFSelect } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
CashSelInt.propTypes = {
    discount: PropTypes.object,
};
export default function CashSelInt({ discount }) {
    const isMountedRef = useIsMountedRef();
    const [allvend, SelectAllVend] = useState([{ key: 'autopilot', name: 'Autopilot' }])
    const NewUserSchema = Yup.object().shape({
        mtn: Yup.string().required('MTN A2C VENDING REQUIRED'),
        airtel: Yup.string().required('AIRTEL A2C VENDING REQUIRED'),
        glo: Yup.string().required('GLO A2C VENDING REQUIRED'),
        mobile: Yup.string().required('9MOBILE A2C VENDING REQUIRED'),
    });
    const defaultValues = useMemo(
        () => ({
            mtn: discount?.cash?.mtn || '',
            airtel: discount?.cash?.airtel || '',
            glo: discount?.cash?.glo || '',
            mobile: discount?.cash?.mobile || '',
        }),
        [discount]
    );
    const AccessToken = window.localStorage.getItem('accessToken');
    const methods = useForm({
        resolver: yupResolver(NewUserSchema),
        defaultValues,
    });
    const {
        reset,
        setError,
        handleSubmit,
        formState: { errors, isSubmitting },
    } = methods;

    useEffect(() => {
        if (discount?.cash !== undefined) {
            reset(defaultValues);
            SelectAllVend(discount?.habukhan_code)
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [discount]);

    const onSubmit = async (data) => {
        try {
            await axios.post(`/api/edit/cashsel/account/${AccessToken}/habukhan/secure`, data)
            swal('success', `Updated`, 'success');
        } catch (error) {
            if (isMountedRef.current) {
                setError('afterSubmit', error);
            }
        }
    };


    return (
        <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
            <Grid container spacing={3}>

                <Grid item xs={12} md={8}>
                    {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
                    <Card sx={{ p: 3 }}>

                        <Box
                            sx={{
                                display: 'grid',
                                columnGap: 3,
                                rowGap: 3,
                                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
                            }}
                        >
                            <RHFSelect name="mtn" label="MTN A2C" defaultValue="autopilot">
                                <option value="" />
                                {allvend.map((option) => (
                                    <option key={option.key} value={option.key}>
                                        {option.name}
                                    </option>
                                ))}
                            </RHFSelect>

                            <RHFSelect name="airtel" label="AIRTEL A2C" defaultValue="autopilot">
                                <option value="" />
                                {allvend.map((option) => (
                                    <option key={option.key} value={option.key}>
                                        {option.name}
                                    </option>
                                ))}
                            </RHFSelect>
                            <RHFSelect name="glo" label="GLO A2C" defaultValue="autopilot">
                                <option value="" />
                                {allvend.map((option) => (
                                    <option key={option.key} value={option.key}>
                                        {option.name}
                                    </option>
                                ))}
                            </RHFSelect>
                            <RHFSelect name="mobile" label="9MOBILE A2C" defaultValue="autopilot">
                                <option value="" />
                                {allvend.map((option) => (
                                    <option key={option.key} value={option.key}>
                                        {option.name}
                                    </option>
                                ))}
                            </RHFSelect>

                        </Box>
                        <Stack alignItems="flex-end" sx={{ mt: 3 }}>
                            <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                                Proceed
                            </LoadingButton>
                        </Stack>
                    </Card>
                </Grid>
            </Grid>
        </FormProvider>
    );
}
